import { store } from '../redux/store';
import client from '../shared/configAxios';
import { logger } from '../utils';
import { loginWorker, profileWorker } from './endpoint';
import DeviceInfo from 'react-native-device-info';

export const fetchLogin = async (payload: {
  username: string;
  password: string;
}) => {
  try {
    const fcmToken = store?.getState()?.auth?.fcmToken;
    const deviceId = await DeviceInfo.getUniqueId();
    const deviceName = await DeviceInfo.getDeviceName();
    const response = await client.post(loginWorker, {
      ...payload,
      deviceToken: fcmToken,
      deviceId,
      deviceName,
    });
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchLogin', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};

export const fetchProfile = async () => {
  try {
    const response = await client.get(profileWorker);

    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchProfile', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};
