import { call, put, takeEvery } from 'redux-saga/effects';
import { logger } from '../../../utils';
import { HIDE_LOADING_OVERLAY, SHOW_LOADING_OVERLAY } from '../app/constants';
import { fetchLogin, fetchProfile } from '../../../api/login';
import { ResponseApi } from '../../store/types';
import {
  LOGIN_ASYNC,
  LOGIN_FAILED,
  LOGIN_SUCCESS,
  SET_USER_PROFILE,
} from './constans';
import { fetchPresignedUrl } from '../../../api/app';

function* handleLogin(action: {
  type: string;
  payload: any;
}): Generator<any, void, any> {
  try {
    yield put({ type: SHOW_LOADING_OVERLAY });
    yield put({ type: LOGIN_FAILED, payload: null });

    const response = (yield call(fetchLogin, action.payload)) as ResponseApi;

    if (response.status !== 200) {
      yield put({
        type: LOGIN_FAILED,
        payload: response.message || response.data?.message,
      });
      return;
    }
    yield put({
      type: LOGIN_SUCCESS,
      payload: {
        token: response.data.token,
        refreshToken: response.data.refreshToken,
      },
    });

    const getProfile = (yield call(fetchProfile)) as ResponseApi;

    if (getProfile.status !== 200) {
      yield put({
        type: LOGIN_FAILED,
        payload: getProfile.message || getProfile.data?.message,
      });
      return;
    }
    let userProfile = getProfile.data;
    // Fetch presigned URL for identification photo if exists
    const photoKey =
      userProfile?.identificationNumbers?.[0]?.identificationNumberPhoto?.key;

    if (photoKey) {
      try {
        const encodedPhotoKey = encodeURIComponent(photoKey);
        const imgResponse = (yield call(
          fetchPresignedUrl,
          encodedPhotoKey,
        )) as ResponseApi;

        if (
          imgResponse?.status === 200 &&
          userProfile?.identificationNumbers?.[0]
        ) {
          userProfile.identificationNumbers[0].imgLink = imgResponse.data;
        }
      } catch (error) {
        logger('Error fetching identification photo:', error);
      }
    }

    yield put({
      type: SET_USER_PROFILE,
      payload: userProfile,
    });
  } catch (error) {
    logger('error handleLogin', error);
    yield put({
      type: LOGIN_FAILED,
      payload: error instanceof Error ? error.message : 'Login failed',
    });
  } finally {
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
}

export default function* appSaga(): Generator<any, void, any> {
  yield takeEvery(LOGIN_ASYNC, handleLogin);
}
